
import { defineComponent, onMounted, ref } from "vue";
import SignaturePad from "signature_pad";
import { Toast } from "vant";
import * as FileSaver from "file-saver";

export default defineComponent({
  name: "App",

  setup() {
    const wordRef = ref<HTMLElement>();
    const paintRef = ref<HTMLCanvasElement>();
    let signaturePad: SignaturePad;
    const words = ref<Array<string>>("".split(""));
    const index = ref<number>(0);
    let imgs = Array(words.value.length).fill("");

    const isPreview = ref(false);
    const previewImgs = ref<Array<string>>([]);

    const isDialog = ref(false);
    const inputValue = ref<string>();

    const isSelect = ref(false);
    const selectValue = ref();
    const selectOptions = [
      {
        text: "李白",
        value: "1",
        children: [
          {
            text: "早发白帝城",
            value:
              "朝辞白帝彩云间，千里江陵一日还。两岸猿声啼不住，轻舟已过万重山。",
          },
          {
            text: "黄鹤楼送孟浩然之广陵",
            value:
              "故人西辞黄鹤楼，烟花三月下扬州。孤帆远影碧空尽，唯见长江天际流。",
          },
          {
            text: "静夜思",
            value: "床前明月光，疑是地上霜。举头望明月，低头思故乡。",
          },
          {
            text: "赠汪伦",
            value:
              "李白乘舟将欲行，忽闻岸上踏歌声。桃花潭水深千尺，不及汪伦送我情",
          },
          {
            text: "望庐山瀑布",
            value:
              "日照香炉生紫烟，遥看瀑布挂前川。飞流直下三千尺，疑是银河落九天。",
          },
        ],
      },
      {
        text: "杜甫",
        value: "2",
        children: [
          {
            text: "望岳",
            value:
              "岱宗夫如何?齐鲁青未了。造化钟神秀，阴阳割昏晓。荡胸生曾云，决眦入归鸟。会当凌绝顶，一览众山小。",
          },
          {
            text: "绝句",
            value:
              "两个黄鹂鸣翠柳，一行白鹭上青天。窗含西岭千秋雪，门泊东吴万里船。",
          },
          {
            text: "春夜喜雨",
            value: "好雨知时节，当春乃发生。随风潜入夜，润物细无声。",
          },
        ],
      },
    ];

    const isSign = ref(false);
    const signValue = ref<string>();

    function onSelect() {
      isSelect.value = false;

      words.value = selectValue.value.split("");
      index.value = 0;
      imgs = Array(words.value.length).fill("");
      selectValue.value = "";
      onClear();
      if (wordRef.value) {
        wordRef.value.scrollLeft = 0;
      }
    }

    function onCloseSelect() {
      isSelect.value = false;
    }

    function toSelectWord() {
      isSelect.value = true;
    }

    function resizeCanvas() {
      const ratio = Math.max(window.devicePixelRatio || 1, 1);
      paintRef.value!.width = paintRef.value!.offsetWidth * ratio;
      paintRef.value!.height = paintRef.value!.offsetHeight * ratio;
      paintRef.value!.getContext("2d")?.scale(ratio, ratio);
      signaturePad!.clear();
    }

    onMounted(() => {
      signaturePad = new SignaturePad(paintRef.value!, {
        minWidth: 6,
        maxWidth: 16,
      });

      setTimeout(() => {
        resizeCanvas();
        window.onresize = () => resizeCanvas();
      }, 100);
    });

    function onWord(i: number) {
      if (i !== index.value) {
        _saveImg();
        onClear();
        index.value = i;
        if (wordRef.value) {
          wordRef.value.scrollLeft = Math.max(index.value * 48 - 180, 0);
        }
      }
    }

    function onLast() {
      _saveImg();
      if (index.value > 0) {
        index.value--;
        onClear();
        if (wordRef.value) {
          wordRef.value.scrollLeft = Math.max(index.value * 48 - 180, 0);
        }
      }
    }

    function onNext() {
      _saveImg();
      if (index.value < words.value.length - 1) {
        index.value++;
        onClear();
        if (wordRef.value) {
          wordRef.value.scrollLeft = Math.max(index.value * 48 - 180, 0);
        }
      }
    }

    function onClear() {
      signaturePad.clear();
    }

    function onCreateImg() {
      _saveImg();
      _createImg();
    }

    function _saveImg() {
      if (!signaturePad.isEmpty()) {
        imgs[index.value] = signaturePad.toDataURL();
      }
    }

    function _createImg(sign?: string) {
      let count = words.value.length;

      const canvas = document.createElement("canvas");
      canvas.width = 200 * (count >= 8 ? 8 : count);
      canvas.height = 200 * Math.ceil(count / 8);
      const ctx = canvas.getContext("2d");

      if (sign) {
        canvas.height += 200;
      }

      ctx!.fillStyle = "#fff";
      ctx!.fillRect(0, 0, canvas.width, canvas.height);

      if (sign) {
        ctx!.font = "100px " + font.value;
        ctx!.fillStyle = signaturePad.penColor;
        ctx!.fillText(
          sign + " 沐手恭书",
          canvas.width - sign.length * 100 - 480,
          canvas.height - 80
        );
      }

      for (let i = 0; i < imgs.length; i++) {
        const base64 = imgs[i];
        if (base64) {
          const image = new Image();
          image.onload = function () {
            ctx!.drawImage(
              image,
              (i % 8) * 200,
              Math.floor(i / 8) * 200,
              200,
              200
            );
            count--;

            if (count === 0) {
              isPreview.value = true;
              previewImgs.value = [canvas.toDataURL()];
            }
          };
          image.src = base64;
        } else {
          count--;

          if (count === 0) {
            isPreview.value = true;
            previewImgs.value = [canvas.toDataURL()];
          }
        }
      }
    }

    function onInputWord() {
      isDialog.value = true;
    }

    function onConfirmInputWord() {
      if (inputValue.value) {
        isDialog.value = false;
        words.value = inputValue.value.split("");
        index.value = 0;
        imgs = Array(words.value.length).fill("");
        inputValue.value = "";
        onClear();
      } else {
        Toast("请录入字帖");
        return false;
      }
    }

    function onCancelInputWord() {
      inputValue.value = "";
    }

    const widthColumns = ["楷体", "隶书", "硬笔"];
    const isWidth = ref(false);

    function toSelectWidth() {
      isWidth.value = true;
    }

    function onConfirmWidth(w: number) {
      isWidth.value = false;
      signaturePad.minWidth = w;
      signaturePad.maxWidth = w + 10;
    }

    const font = ref<string>("kaiti");
    const isFont = ref(false);
    const fontColumns = ["楷体", "隶书", "硬笔"];
    function toSelectFont() {
      isFont.value = true;
    }
    function onConfirmFont(v: string) {
      font.value = { 楷体: "kaiti", 隶书: "lishu", 硬笔: "yingbi" }[v] || "";
      isFont.value = false;
    }

    const colorColumns = ["黑色", "红色"];
    const isColor = ref(false);

    function toSelectColor() {
      isColor.value = true;
    }

    function onConfirmColor(c: string) {
      isColor.value = false;
      signaturePad.penColor = { 黑色: "#00", 红色: "#f00" }[c] || "#00";
    }

    function toAddSign() {
      isSign.value = true;
      signValue.value = "";
    }

    function onAddSign() {
      _createImg(signValue.value);
    }

    function onCancelSign() {
      isSign.value = false;
    }

    function saveImgToLocal() {
      FileSaver.saveAs(previewImgs.value[0], `linmo-${+new Date()}.png`);
    }

    function toShareImg() {
      let mateArr = [
        "og:url",
        "https://tianma630.com/linmo/dist/index.html",
        "og:title",
        "临摹",
        "og:image",
        "https://img.zcool.cn/community/01bc7b56000a516ac7251df8ded6a4.JPG@1280w_1l_2o_100sh.jpg",
        "og:type",
        "website",
      ];
      window.open(
        "http://www.facebook.com/sharer.php?u=" +
          encodeURIComponent(
            `http://java.chendahai.cn/share/new?meta=${mateArr.toString()}`
          )
      );
    }

    return {
      wordRef,
      paintRef,
      words,
      index,
      onWord,
      onLast,
      onNext,
      onClear,
      onCreateImg,
      isPreview,
      previewImgs,
      onInputWord,
      isDialog,
      inputValue,
      onConfirmInputWord,
      onCancelInputWord,

      isSelect,
      selectValue,
      selectOptions,
      toSelectWord,
      onSelect,
      onCloseSelect,

      widthColumns,
      isWidth,
      toSelectWidth,
      onConfirmWidth,

      colorColumns,
      isColor,
      toSelectColor,
      onConfirmColor,

      isSign,
      signValue,
      toAddSign,
      onAddSign,
      onCancelSign,
      saveImgToLocal,
      toShareImg,

      font,
      isFont,
      fontColumns,
      toSelectFont,
      onConfirmFont,
    };
  },
});
